import { useEffect, useRef, useState } from 'react';
import './App.css';
import './styles.scss';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { gsap } from 'gsap';

const App = () => {
  const modelRef = useRef(null); //   
  const containerRef = useRef(null); //   
  const textRef = useRef(null); //   
  const [scrollProgress, setScrollProgress] = useState(0); //  

  useEffect(() => {
    // ===  ===
    const scene = new THREE.Scene();

    // 
    const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
    camera.position.set(0, 2, 5);

    // 
    const renderer = new THREE.WebGLRenderer();
    renderer.setSize(window.innerWidth, window.innerHeight);
    containerRef.current.appendChild(renderer.domElement);

    //    
    renderer.setClearColor(0xffffff, 1);

    // 
    const light = new THREE.DirectionalLight(0xffffff, 1);
    light.position.set(5, 10, 5).normalize();
    scene.add(light);

    const ambientLight = new THREE.AmbientLight(0x404040, 1);
    scene.add(ambientLight);

    // ===  3D- ===
    const loader = new GLTFLoader();
    loader.load(
      '/model/porsche.glb',
      (gltf) => {
        const model = gltf.scene;
        model.scale.set(1.8, 1.8, 1.8);
        model.position.set(2, -0.6, 0);
        scene.add(model);
        modelRef.current = model; //    
      },
      undefined,
      (error) => {
        console.error('Error loading model:', error);
      }
    );

    //  
    const animate = () => {
      requestAnimationFrame(animate);
      renderer.render(scene, camera);
    };
    animate();

    //  
    return () => {
      renderer.dispose();
      if (containerRef.current && renderer.domElement) {
        // eslint-disable-next-line react-hooks/exhaustive-deps
        containerRef.current.removeChild(renderer.domElement);
      }
    };
  }, []);

  // ===     ===
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const handleWheel = (e) => {
    //     
    const delta = e.deltaY;

    //        
    setScrollProgress((prevProgress) => {
      let newProgress = prevProgress + delta * 0.002; // 0.002 -   
      newProgress = Math.max(0, Math.min(newProgress, 1)); //    0  1
      return newProgress;
    });

    if (modelRef.current) {
      //   ,   
      gsap.to(modelRef.current.position, {
        x: 2 - scrollProgress * 5.5, //   1.5  -1.5
        y: -0.6 + scrollProgress * 0.1, 
        duration: 0.5,
        ease: 'power2.out',
      });
      gsap.to(modelRef.current.rotation, {
        x: scrollProgress * 0.125, //   0  +0.3
        y: -scrollProgress * 1.4, //   0  -1.6
        duration: 0.5,
        ease: 'power2.out',
      });
      gsap.to(modelRef.current.scale, {
        x: 1.8 - scrollProgress * 0.25, //   1.8  1
        y: 1.8 - scrollProgress * 0.25,
        z: 1.8 - scrollProgress * 0.25,
        duration: 0.5,
        ease: 'power2.out',
      });
      
    }
    if (textRef.current && scrollProgress > 0.2) {
      gsap.to(textRef.current, {
        opacity: 1,
        y: 0,
        x: 0, //    
        duration: 0,
        ease: 'power2.out',
      });
    }
    //      
    if (textRef.current) {
      if (scrollProgress < 0.8) {
        gsap.to(textRef.current, {
          opacity: 0,
          x: +300, //   
          duration: 0.9,
          ease: 'power2.out',
        });
      }
    }
  };

  useEffect(() => {
    //     
    window.addEventListener('wheel', handleWheel);

    return () => {
      window.removeEventListener('wheel', handleWheel);
    };
  }, [handleWheel, scrollProgress]);

  return (
    <div ref={containerRef} className="container">
      <h1 className="title">Porsche 911</h1>
      <h3
        ref={textRef}
        className="info-text"
        style={{ opacity: 0,  position: 'absolute', top: '40%', left: '55%' }}
      >
         911 Turbo.     ,       .
         911 Turbo    .
      </h3>
    </div>
  );
};

export default App;
